@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: "Open Sans", serif;
}

.wrapper {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

  background: rgba(45,176,224,1);
  background: -webkit-gradient(left bottom, right top, color-stop(0%, rgba(45,176,224,1)), color-stop(100%, rgba(96,40,168,1)));
  background: linear-gradient(45deg, rgba(45,176,224,1) 0%, rgba(96,40,168,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#2db0e0', endColorstr='#6028a8', GradientType=1 );
}

.main {
	min-height: 500px;
  background: #2a6bb1;
  box-shadow: 0px 2px 80px -20px rgba(0,0,0,0.5);
  width: 60%;
	margin: 0 auto;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	position: relative;
}

.left, .right {
	height: 100%;
}

.info {
	min-height: 500px;
	background: url(/forecast-app/static/media/polygon.951d3ec7.jpg) center no-repeat;
	color: #fff;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	
}
.info__title {
	margin: auto;
	padding: 0 40px;
}
.form {
	padding-left: 50px;
	padding-top: 20px;
	position: relative;
	max-width: 300px;
}
.form__input {
  margin-bottom: 30px;
  padding: 22px 30px 22px 30px;
  border: 2px solid transparent;
  border-radius: 30px;
  background-color: #f0f4f7;
  max-width: 100%;
  cursor: pointer;
  outline: none;
}
.form__input:focus:valid {
  border: 2px solid #6bca3e;
  background: #fff;
}
.form__input:focus:placeholder-shown {
  background-color: #fff;
  border: 2px solid #e2eaf0;
}
.form__input:hover {
  background-color: #e2eaf0;
}
.form__submit {
  position: absolute;
  top: 33px;
  right: 10px;
  border-radius: 50%;
  width: 44px;
  height: 44px;
  border: 2px solid #e2eaf0;
  background: url(/forecast-app/static/media/Vector.91c050a9.svg) center no-repeat;
  background-color: #fff;
  transition: all 0.25s ease;
  outline: none;
}
.form__submit:hover {
  border: 2px solid #6bca3e;
  background: url(/forecast-app/static/media/Vector_green.9f1ef7bf.svg) center no-repeat;
  background-color: #fff;
}


.weather {
	padding-left: 60px;
	margin-top: 5px;
	color: white;
	font-size: 20px;
}
.weather__error {
	color: #F7DBCB;
}
.weather__forecast {
	margin-top: 10px;
}
